import './Book.css'
import { useEffect, useState } from 'react';

function Chapter(props){
    const {itemChapter, onDeleteChapter, onSaveChapter, itemBook}=props
    const [isEditing, setIsEditing]=useState(false)
const[title,setTitle]=useState(itemChapter.title)
const[content,setContent]=useState(itemChapter.content)


const deleteChapter=(evt)=>{
    console.log(itemBook)
    onDeleteChapter(itemBook, itemChapter.id)
}
// const saveBook=(evt)=>{
//     onSave(item.id, {
//         title,
//         content
//     })
//     setIsEditing(false)
// }

const editChapter=()=>{
    setIsEditing(true)

}

// const cancel=()=>{
//     setIsEditing(false)
// }

// const details=()=>{
// onSelect(item.id)
// }

    return(
<div>
    { isEditing
        ?(
            <div>
            Chapter
             <input type='text' value={title} onChange={(event)=> setTitle(event.target.value)}></input>
 with content
 <input type='text'  value={content} onChange={(event)=> setContent(event.target.value)}></input>

             {/* <input type='button' value={'save'} onClick={saveBook} />

        <input type='button' value={'cancel'} onClick={cancel} /> */}

            </div>
        )
        :
        ( <div>
           Chapter title  <span className="title">{itemChapter.title}</span> | Chapter content <span style={{backgroundColor:'lightblue'}}>{itemChapter.content}</span>
        <input type='button' value={'delete'} onClick={deleteChapter} />
        <input type='button' value={'edit'} onClick={editChapter} />
   
        </div>
        )
    }
 </div>
    )
}

export default Chapter