import {useParam, useParams} from 'react-router-dom'
import { useEffect, useState } from 'react';
import store from './BookStore'
import BookAddForm from './BookAddForm'
import Book from './Book'
import Chapter from './Chapter'
import axios from "axios";
import ChapterAddForm from './ChaperAddForm';

function BookDetails(props) {
    const {id,onCancel}=props
    const params=useParams()
    const [title, setTitle]=useState("")
    const [content, setContent]=useState("")
    const [chapters, setChapters]=useState([])
    const idBook=sessionStorage.getItem("id")

    useEffect(()=>{
        let path=`http://localhost:8080/books/${props.id}`
        axios.get(path).then((response)=>{
            setTitle(response.data.title);
            setContent(response.data.content)
            console.log(response)

        })
    })

    useEffect(()=>{
        console.log(props.id)
        store.getBookChapters(props.id)
        store.emitter.addListener('GET_CHAPTERS_SUCCESS', ()=>{
          setChapters(store.data)
          console.log(chapters)
        })
      },[])


const cancel=()=>{
onCancel();
}


const deleteChapter=(id,cid)=>{
    store.deleteChapter(id, cid)
  }

  return (
    <div>
     Details for the company: {id}
     <div>
         Title: {title}
        
     </div>
     <div>
         
         Content:{content}
     </div>
     <div>
         Chapters:{chapters.map(e=><Chapter key={e.id} itemBook={id} itemChapter={e} onDeleteChapter={deleteChapter}></Chapter>)}
     </div>
     <div>  <button value="cancel" onClick={cancel}>Cancel </button></div>
     
     <div>
       <ChapterAddForm></ChapterAddForm>
    </div>
    </div>
    
  );
}

export default BookDetails;
