
import { useEffect, useState } from 'react';
import store from './BookStore'
import BookAddForm from './BookAddForm'
import Book from './Book'
import BookDetails from './BookDetails'

function BookList() {
const[isSelected, setIsSelected]=useState(false)
  const [books, setBooks]=useState([])

  const[book, setBook]=useState([])

  const [counter, setCounter]=useState(0)
  useEffect(()=>{
    store.getBooks()
    store.emitter.addListener('GET_BOOKS_SUCCESS', ()=>{
      setBooks(store.data)
      
    })
  },[])

  const addBook=(book)=>{
    store.addBook(book)
  }
const deleteBook=(id)=>{
  store.deleteBook(id)
}
const saveBook=(id, book)=>{
  store.saveBook(id, book)
}

const selectBook=(id)=>{
    console.log(id)
   setCounter(id)
    setIsSelected(true);

   sessionStorage.setItem("id", id)

    console.log(book)
    
    
// store.getBook(id)

}

const cancel=()=>{
    setIsSelected(false)
}

  return (
    <div>
      {/* <h4>List of books</h4>
      {
        books.map(e=> <Book key={e.id} item={e} onDelete={deleteBook} onSave={saveBook} onSelect={selectBook}></Book>)
      }
      <BookAddForm onAdd={addBook}/> */}

{ isSelected
        ?(
            <div>
                <BookDetails onCancel={cancel} id={counter} ></BookDetails>
                
                
            </div>
        )
        :
        ( <div>
           <h4>List of books</h4>
      {
        books.map(e=> <Book key={e.id} item={e} onDelete={deleteBook} onSave={saveBook} onSelect={selectBook}></Book>)
      }
      <BookAddForm onAdd={addBook}/>
        </div>
        )
    }
    </div>
  );
}

export default BookList;
