import './Book.css'
import { useEffect, useState } from 'react';

function Book(props){
    const {item, onDelete, onSave, onSelect}=props
    const [isEditing, setIsEditing]=useState(false)
const[title,setTitle]=useState(item.title)
const[content,setContent]=useState(item.content)


const deleteBook=(evt)=>{
    onDelete(item.id)
}
const saveBook=(evt)=>{
    onSave(item.id, {
        title,
        content
    })
    setIsEditing(false)
}

const edit=()=>{
    setIsEditing(true)

}

const cancel=()=>{
    setIsEditing(false)
}

const details=()=>{
onSelect(item.id)
}

    return(
<div>
    { isEditing
        ?(
            <div>
             i am a book title 
             <input type='text' value={title} onChange={(event)=> setTitle(event.target.value)}></input>
 with content
 <input type='text'  value={content} onChange={(event)=> setContent(event.target.value)}></input>

             <input type='button' value={'save'} onClick={saveBook} />

        <input type='button' value={'cancel'} onClick={cancel} />

            </div>
        )
        :
        ( <div>
            i am a book title <span className="title">{item.title}</span> with content <span style={{backgroundColor:'lightblue'}}>{item.content}</span>
        <input type='button' value={'delete'} onClick={deleteBook} />
        <input type='button' value={'edit'} onClick={edit} />
        <input type='button' value={'details'} onClick={details} />
        </div>
        )
    }
 </div>
    )
}

export default Book