import {useState} from 'react'

function ChapterAddForm(props){
    // const {onAdd}=props
    const [title, setTitle]=useState('')
    const [content, setContent] =useState('')

// const add=(evt)=>{
//     onAdd({
//         title, 
//         content
//     })
// }

    return (
        <div> 
            <h6> Add a chapter </h6>
           <div> 
               <input type='text' placeholder='title' onChange={(event)=> setTitle(event.target.value)}></input>
           </div>
           <div> 
               <input type='text' placeholder='content' onChange={(event)=> setContent(event.target.value)}></input>
           </div>
           <div> 
               <input type='button' value='add me!' ></input>
           </div>
        </div>
    )
}

export default ChapterAddForm