import {EventEmitter} from 'fbemitter'

//  const SERVER="http://localhost:8080"
const SERVER=`${window.location.protocol}//${window.location.hostname}:${window.location.port}`

 class BookStore{
     constructor(){
         this.data=[]
         this.emitter=new EventEmitter()
     }

     async getBooks(){
         try{
            const response=await fetch(`${SERVER}/books`)
            if(!response.ok){
                throw response
            }
            this.data=await response.json()
            this.emitter.emit('GET_BOOKS_SUCCESS');
         }catch(err){
            console.warn(err)
            this.emitter.emit('GET_BOOKS_ERROR');
        }
         
     }

     async getBook(id){
        try{
           const response=await fetch(`${SERVER}/books/${id}`)
           if(!response.ok){
               throw response
           }
           this.data=await response.json()
           this.emitter.emit('GET_BOOK_SUCCESS');
        }catch(err){
           console.warn(err)
           this.emitter.emit('GET_BOOK_ERROR');
       }
        
    }

     async addBook(book){
        try{
            const response=await fetch(`${SERVER}/books`,{
                method:'POST',
                headers:{
                    'Content-Type':"application/json"
                },
                body: JSON.stringify(book)
            })
            if(!response.ok){
                throw response
            }
          this.getBooks()
         }catch(err){
            console.warn(err)
            this.emitter.emit('ADD_BOOK_ERROR');
        }
     }

     async saveBook(id, book){
        try{
            const response=await fetch(`${SERVER}/books/${id}`,{
                method:'PUT',
                headers:{ 
                    'Content-Type':"application/json"
                },
                body: JSON.stringify(book)
            })
            if(!response.ok){
                throw response
            }
          this.getBooks()
         }catch(err){
            console.warn(err)
            this.emitter.emit('UPDATE_BOOK_ERROR');
        }
     }

     async deleteBook(id){
        try{
            const response=await fetch(`${SERVER}/books/${id}`,{
                method:'DELETE',
               
            })
            if(!response.ok){
                throw response
            }
          this.getBooks()
         }catch(err){
            console.warn(err)
            this.emitter.emit('DELETE_BOOK_ERROR');
        }
     }
//-------------------CHAPTERS
     async getBookChapters(id){
        try{
           const response=await fetch(`${SERVER}/books/${id}/chapters`)
           if(!response.ok){
               
               throw response
           }
           
           this.data=await response.json()
           this.emitter.emit('GET_CHAPTERS_SUCCESS');
        }catch(err){
           console.warn(err)
           this.emitter.emit('GET_CHAPTERS_ERROR');
       }
        
    }

    async getBookChapter(id, cid){
        try{
           const response=await fetch(`${SERVER}/books/${id}/chapters/${cid}`)
           if(!response.ok){
               throw response
           }
           this.data=await response.json()
           this.emitter.emit('GET_CHAPTER_SUCCESS');
        }catch(err){
           console.warn(err)
           this.emitter.emit('GET_CHAPTER_ERROR');
       }
        
    }


    async addChapter(id,chapter){
        try{
            const response=await fetch(`${SERVER}/books/${id}/chapters`,{
                method:'POST',
                headers:{
                    'Content-Type':"application/json"
                },
                body: JSON.stringify(chapter)
            })
            if(!response.ok){
                throw response
            }
          this.getBookChapters()
         }catch(err){
            console.warn(err)
            this.emitter.emit('ADD_CHAPTER_ERROR');
        }
     }

     async saveChapter(id, chapter, cid){
        try{
            const response=await fetch(`${SERVER}/books/${id}/chapters/${cid}`,{
                method:'PUT',
                headers:{ 
                    'Content-Type':"application/json"
                },
                body: JSON.stringify(chapter)
            })
            if(!response.ok){
                throw response
            }
          this.getBookChapters()
         }catch(err){
            console.warn(err)
            this.emitter.emit('UPDATE_CHAPTER_ERROR');
        }
     }

     async deleteChapter(id, cid){
        try{
            const response=await fetch(`${SERVER}/books/${id}/chapters/${cid}`,{
                method:'DELETE',
               
            })
            if(!response.ok){
                throw response
            }
          this.getBookChapters()
         }catch(err){
            console.warn(err)
            this.emitter.emit('DELETE_CHAPTER_ERROR');
        }
     }

 }

 const store=new BookStore()
 export default store;